import { logData } from './log'
import { isFunc } from './method'
import { sanitize } from './string'

export const cloneJson = obj => {
  try {
    return JSON.parse(JSON.stringify(obj))
  }
  catch(e){
    logData(e.message, 'error')
    return null
  }
}

export const clearObj = (obj, filter) => {
  obj && Object
    .entries(obj)
    .map(([key, value]) => {
      if(filter && filter.indexOf(key) !== -1) return
      if(typeof value === 'object') clearObj(value)
      obj[key] = undefined
      delete obj[key]
    })
}

export const isObj = obj => typeof obj === 'object' && !Array.isArray(obj)

export const deepMerge = (...sources) => (
  sources.reduce(
    (merged, source) =>
      source instanceof Array
        ?
        [ ...((merged instanceof Array && merged) || []), ...source ]
        :
        source instanceof Object
          ? Object.entries(source)
           
            .reduce(
              (joined, [ key, value ]) => ({
                ...joined,
                [key]:
                 
                 
                 
                  (
                    !isFunc(value) &&
                    value instanceof Object &&
                    key in joined &&
                   
                   
                    deepMerge(joined[key], value)
                  ) ||
                 
                  value
              }),
             
              merged
            )
          :
          merged,
    {}
  )
)

export const mapObj = (obj, cb) => (
  (isObj(obj) && isFunc(cb) &&
  Object
    .entries(obj)
    .map(([ key, value ]) => cb(key, value))
  ) || obj
)

export const reduceObj = (obj, cb) => (
  (isObj(obj) && isFunc(cb) &&
  Object
    .entries(obj)
    .reduce((data, [ key, value ]) => cb(key, value, data), {})
  ) || obj
)

export const deepFreeze = obj => {
  Object.freeze(obj)
  Object
    .getOwnPropertyNames(obj)
    .map(prop => {
      obj.hasOwnProperty(prop)
        && obj[prop] !== null
        && (typeof obj[prop] === 'object' || isFunc(obj[prop]))
        && !Object.isFrozen(obj[prop])
        && deepFreeze(obj[prop])
    })

  return obj
}

export const sanitizeCopy = obj => JSON.parse(sanitize(JSON.stringify(obj)))


export const trimStringFields = object => (
  Object
    .entries(object)
    .reduce((cleaned, [ key, value ]) => {
      cleaned[key] = typeof value === 'string' ? value.trim() : value
      return cleaned
    }, object)
)

export const set = (obj = {}, path, finalValue = {}) => {
  if (!path) return obj
  const isArr = Array.isArray(path)
  if (!isArr && path.indexOf('.') === -1) {
    obj[path] = finalValue
    return obj
  }
  const parts = (isArr && path) || path.split('.')

  return parts.reduce((current, part, index) => {
    if (index === parts.length - 1) {
      current[part] = finalValue
      return obj
    }
    if (typeof current[part] !== 'object') current[part] = {}
    current = current[part]
    return current
  }, obj)
}

export const get = (obj, path) => {
  if (!obj || !path) return obj

  const isArr = Array.isArray(path)
  if (!isArr && path.indexOf('.') === -1) obj[path]
  const parts = (isArr && path) || path.split('.')

  let hasBreak = false
  return parts.reduce((current, part) => {
    if (!current || !current[part] || hasBreak) {
      hasBreak = true
      return undefined
    }
    return current[part]
  }, obj)
}

export const unset = (obj, path) => {
  if (typeof obj !== 'object' || !obj || !path) return undefined
  const parts = Array.isArray(path) ? path : path.split('.')

  const partsCopy = [ ...parts ]
  let current = obj
  parts.map((part, index) => {
    if (index === parts.length - 1) {
      current = reduceObj(
        current,
        (clean, key) => {
          if (key !== part) clean[key] = current[key]
          return clean
        },
        {}
      )
    }
    else if (typeof current[part] === 'object') {
      partsCopy.shift()
      current[part] = removeObjPath(current[part], partsCopy)
    }
  })
  return current
}

export const reduceTargetKeys = (target, keys, predicate) => (
  Object.keys(target).reduce(predicate, {})
)

export const omitKeys = (target = {}, keys = []) => (
  reduceTargetKeys(target, keys, (acc, key) => (
    keys.some(omitKey => omitKey === key)
      ? acc 
      : { ...acc, [key]: target[key] }
  ))
)

export const pickKeys = (target = {}, keys = []) => (
  reduceTargetKeys(target, keys, (acc, key) => (
    keys.some(pickKey => pickKey === key)
      ? { ...acc, [key]: target[key] }
      : acc
  ))
)

export const jsonEqual = (one, two) => {
  try {
    return JSON.stringify(one) === JSON.stringify(two)
  }
  catch(e){
    return false
  }
}
